<view class="zhengwen hei_26">
	<image src="../../static/img/banner5.jpg" mode="widthFix"></image>
	<view class="hei_26">
		可能大家在看刑法当中的法律条文的时候都可以发现在很多的犯罪量刑的条文规定中可以看到没收个人全部财产。当然，这种没收的话，它是分为几种方式的，比如说有并处没收个人全部财产。因此，有人想要了解一下。并处没收个人全部财产的规定是什么?
	</view>
	<view class="xq_zhengwen_title hei_26_bold">
		一、并处没收个人全部财产的规定是什么?
	</view>
	<view class="hei_26">
		必并制，指在判处其他刑罚的同时必须同时并处没收财产。
		例如，刑法第383条规定，对犯贪污罪的，个人贪污数额在
		10万元以上的，情节特别严重的，处死刑，并处没收财产
		</view>
		<br/>
		<view class="hei_26">
			没收财产没收的是哪些财产
		</view>
		<br/>
		<view class="">
			
			(一)没收财产不同于罚金，也不同于追缴非法所得、没收违
			禁品和供犯罪所用的本人财物。罚金是较轻的财产刑，适用
			于情节较轻的犯罪;没收财产是较重的财产刑，适用于情节较
			重的犯罪。罚金是让犯罪分子缴纳一定的金钱;没收财产剥夺
			的是犯罪分子个人所有财产的一部或全部，可以是金钱，也
			可以是其他财产(包括动产和不动产)。没收财产是刑罚方法，
			追缴非法所得、没收违禁品和犯罪物品不是刑罚方法。任何
			犯罪分子非法所得的财物，都应当予以追缴或者责令退赔。
			 对于犯罪分子本人所有的财产是没收一部分还是全部，应当
			根据犯罪的性质、情节、社会的危害程度以及案件的具体情
			况确定，不论是没收一部分还是全部，都应当对没收的财产
			名称、数量等在判决中写明，不能笼统地写判决没收被告人
			财产一部分或者全部。
		</view>
		<br/>
		<view class="">
			(二)没收财产是没收犯罪分子个人所有财产的一部或者全部。
			没收的只能是属于犯罪分子本人所有的财产，包括犯罪分子
			本人所有的财物及其在与他人共有财产中依法应有的份额。
			在判处没收财产的时候，不得没收属于犯罪分子家属所有或
			者应有的财产。这是我国罪责自负不株连他人原则的具体体
			现和要求。所谓犯罪分子家属所有财产，是指所有权明确属
			于犯罪分子家属本人专有的财产。犯罪分子家属应有的财产，
			是指在共有财产中应归犯罪分子享有的份额。此外，如果犯
			罪分子家属没有其他生活来源，原来就依靠犯罪分子赡养、
			抚养、扶养的，人民法院判决没收财产时，就必须给其留下
			维持生计所必要的财产。这是我国人道主义文明司法的体现，
			也是促使犯罪分子安心改造、安定社会秩序的要求。
		</view>
</view>





			
	
